body {
  margin: 0;
  padding: 0;
}

/* Your .App styles */
.App {
  text-align: center;
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  padding: 20px;
}

/* Responsive adjustment */
@media (max-width: 768px) {
  .App {
    padding: 10px;
  }

  .about-me,
  .my-work,
  .picks,
  .captures {
    margin: 20px auto;
    padding: 15px;
  }

  .about-me h1,
  .my-work h2,
  .picks h2,
  .captures h2 {
    font-size: 22px;
  }

  .about-me p,
  .work-list a,
  .pick-list a {
    font-size: 15px;
  }

  .navbar {
    flex-direction: column;
    gap: 10px;
    padding: 10px 0;
  }

  .pick-list li {
    flex-direction: column;
    align-items: flex-start;
  }

  .pick-thumbnail {
    width: 100%;
    max-width: 120px;
    height: auto;
    margin-bottom: 10px;
  }

  .gallery {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 15px;
  }

  .photo {
    border-radius: 8px;
  }
}

/* About Me Section */
.about-me {
  max-width: 800px;
  margin: 40px auto;
  padding: 20px;
  background: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.about-me h1 {
  font-size: 28px;
  color: #333;
}

.about-me p {
  font-size: 16px;
  line-height: 1.6;
  color: #555;
}

/* Profile Image Styling */
.profile-image {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  margin: 20px auto;
  display: block;
  border: 3px solid #ddd;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

/* My Work Section */
.my-work {
  max-width: 800px;
  margin: 40px auto;
  padding: 20px;
  background: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.my-work h2 {
  font-size: 24px;
  color: #333;
  margin-bottom: 15px;
}

.subheading {
  font-size: 20px;
  margin-top: 30px;
  margin-bottom: 15px;
  color: #444;
  text-align: left;
  padding-left: 10px;
}

.work-list {
  list-style: none;
  padding: 0;
}

.work-list li {
  margin: 10px 0;
}

.work-list a {
  color: #007bff;
  text-decoration: none;
  font-size: 18px;
  font-weight: bold;
  transition: color 0.3s ease-in-out;
}

.work-list a:hover {
  color: #0056b3;
  text-decoration: underline;
}

/* Captures Section */
.captures {
  max-width: 1200px;
  margin: 40px auto;
  text-align: center;
}

.captures h2 {
  font-size: 24px;
  color: #333;
  margin-bottom: 15px;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  margin-top: 20px;
  padding: 0 20px;
}

.photo {
  width: 100%;
  height: auto;
  border-radius: 10px;
  object-fit: cover;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
}

.photo:hover {
  transform: scale(1.05);
}

/* Book and Movie Picks Section */
.picks {
  max-width: 800px;
  margin: 40px auto;
  padding: 20px;
  background: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.picks h2 {
  font-size: 24px;
  color: #333;
  margin-bottom: 15px;
}

.pick-list {
  list-style: none;
  padding: 0;
  margin-top: 10px;
}

.pick-list li {
  display: flex;
  align-items: center;
  gap: 15px;
  margin: 20px 0;
  padding-left: 5px;
  text-align: left;
}

.pick-thumbnail {
  width: 60px;
  height: 90px;
  object-fit: cover;
  border-radius: 8px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
}

.pick-thumbnail:hover {
  transform: scale(1.08);
}

.pick-list a {
  color: #007bff;
  text-decoration: none;
  font-size: 18px;
  font-weight: bold;
  transition: color 0.3s ease-in-out;
}

.pick-list a:hover {
  color: #0056b3;
  text-decoration: underline;
}

html {
  scroll-behavior: smooth;
}

.navbar {
  position: sticky;
  top: 0;
  background: #ffffff;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  gap: 30px;
  z-index: 999;
  box-shadow: 0 2px 5px rgba(0,0,0,0.05);
}

.navbar a {
  font-size: 16px;
  font-weight: bold;
  color: #007bff;
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}

.navbar a:hover {
  color: #0056b3;
}

.divider {
  margin: 60px auto;
  width: 60%;
  border: none;
  border-top: 2px solid #e0e0e0;
}

.connect {
  max-width: 800px;
  margin: 0 auto 30px;
  padding: 10px;
  text-align: center;
}

.connect-links {
  display: flex;
  justify-content: center;
  gap: 30px;
  flex-wrap: wrap;
}

.connect-links a {
  display: flex;
  align-items: center;
  gap: 10px;
  color: #007bff;
  font-size: 17px;
  font-weight: bold;
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}

.connect-links a:hover {
  color: #0056b3;
}

.connect-icon {
  width: 52px;
  height: 22px;
  object-fit: contain;
}

.back-to-top {
  position: fixed;
  bottom: 30px;
  right: 30px;
  background-color: #007bff;
  color: white;
  padding: 10px 14px;
  border-radius: 50%;
  font-size: 20px;
  text-decoration: none;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  transition: background-color 0.3s ease;
  z-index: 1000;
}

.back-to-top:hover {
  background-color: #0056b3;
}